@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:wght@300;500&display=swap');

body {
	/*background: #c8d0d2;*/ 
	background:linear-gradient(135deg, #d9d9d9 0%,#fbfbfb 100%);
	margin: 0;
}

.container {
	/*max-width: 900px;*/
	margin: 1rem 5rem;

}

.card-grid {
	display: grid;
	align-items: center;
	grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
	grid-gap: 1rem;
	justify-content: center;
	background:linear-gradient(135deg, #d9d9d9 0%,#fbfbfb 100%);
}

.card {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	border-radius: 0.25rem;
	box-shadow: 0 0 5px 2px rgba(0,0,0,.3);
	background: white;
	font-family: 'Roboto', sans-serif;
	background:linear-gradient(135deg, #46464b 0%,#212125 100%);
	color: #fff;

	transform: perspective(1000px) rotateY(var(--rotate-Y, 0)) translateY(var(--translate-y,0));
	transform-style: preserve-3d;
	transition: 150ms; 

	cursor: pointer;
}

.card:hover {
	box-shadow: 0 0 5px 2px rgba(0,0,0,.5);
	--translate-y: -2px;
}

.card.flip {
	--rotate-Y: 180deg;
}

.card .front,
.card .back {
	position: absolute;
	padding: 2rem;
	backface-visibility: hidden;
}

.card .back {
	transform: rotateY(180deg);
}

.card .front {
	left: 0;
}

.flashcard-options {
	margin-top: .5rem;
}

.flashcard-option {
	margin-top: .25rem;
	color: #ccc;
	font-size: .75rem;
}

.flashcard-option:first-child {
	margin-top: 0px;
}

.top-level {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: center;
	background:linear-gradient(135deg, #d9d9d9 0%,#fbfbfb 100%);
}

.header {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	
	padding: .75rem;

}

.top-level h1 {
	font-family: 'Pacifico', cursive;
	font-weight: 300px !important;
	font-size: 3em;

}

.form-group {
	margin: .25rem 0;
	display: flex;
	flex-direction: column;
}

.form-group > label {
	color: #777;
	font-size: 1rem;
	margin-bottom: .2rem;
	font-family: 'Roboto', sans-serif;
	font-weight: 300px;
}

.btn {
	background: #3792cb;
	color: #fff;
	padding: .5em 1em;
	border-radius: .5em;
	cursor: pointer;
	margin-top: .5rem;
	outline: hidden;
	font-family: 'Roboto', sans-serif;
	font-weight: 300px;
}

.btn:hover {
	background: #1c4966;
}

.iconID {
	position: absolute;
	top: 0;
	margin-bottom: .2rem;
	filter: invert(1);
}

input[type="number"] {
	padding: .1rem .5rem;
}

select {
	padding: .1rem .5rem;
	background: #fff;
}